import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import profile from "../assets/img/profile.jpg";

export const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const toRotate = ["Bonjour, je m'appelle Melvin, ingnieur en systmes embarqus"];
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(100);
    const [showParagraphs, setShowParagraphs] = useState([false, false, false]);  // Grer l'affichage des paragraphes
    const period = 1000;
    const [isTextComplete, setIsTextComplete] = useState(false); // Nouvel tat pour savoir si l'H1 est complet

    useEffect(() => {
        const ticker = setInterval(() => {
            tick();
        }, delta);

        return () => clearInterval(ticker);
    }, [text, delta]);

    useEffect(() => {
        // Vrifier si l'H1 est complet pour dmarrer l'affichage des paragraphes
        if (isTextComplete) {
            const timeout = setTimeout(() => {
                setShowParagraphs([true, false, false]);
            }, 1000); // Afficher le premier paragraphe aprs 1 seconde

            setTimeout(() => {
                setShowParagraphs([true, true, false]);
            }, 2500); // Afficher le deuxime aprs 1.5 secondes

            setTimeout(() => {
                setShowParagraphs([true, true, true]);
            }, 4000); // Afficher le troisime aprs 2 secondes

            return () => clearTimeout(timeout);
        }
    }, [isTextComplete]); // Effect uniquement aprs que le texte soit complet

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = fullText.substring(0, text.length + 1);

        setText(updatedText);

        if (updatedText === fullText) {
            setIsTextComplete(true); // Le texte est maintenant complet
            setTimeout(() => {
                setLoopNum(loopNum + 1);
                setDelta(period);
            }, period);
        }
    };

    return (
        <section className="banner" id="home">
            <Container>
                <Row className="align-items-center justify-content-center">
                    <Col xs={12} md={8} xl={7}>
                        <span className="tagline">Welcome to my Portfolio</span>
                        <div className="banner-content">
                            <img
                                src={profile}
                                alt="Profil"
                                className="profile-image"
                            />
                            <div className="text-content">
                                <h1>{text}</h1>
                                <p className={showParagraphs[0] ? "show" : ""}>
                                    Attir par llectronique et les systmes embarqus, jai toujours eu une curiosit naturelle pour la manire dont les technologies interagissent et sintgrent dans des environnements complexes. Japprcie particulirement le dveloppement de solutions embarques, alliant programmation, intgration de capteurs et optimisation de systmes en temps rel.
                                </p>
                                <p className={showParagraphs[1] ? "show" : ""}>
                                    Mon exprience ma permis de travailler sur des projets varis, en dveloppant des logiciels embarqus, en amliorant des interfaces et en testant des systmes pour assurer leur performance et leur fiabilit. 
                                </p>
                                <p className={showParagraphs[2] ? "show" : ""}>
                                    Toujours en qute de nouveaux dfis, jaime dcouvrir de nouvelles approches et rester stimul, que ce soit dans mon travail,  travers mes voyages ou dans ma pratique du sport. Ces expriences mont appris  madapter rapidement,  travailler en quipe et  relever des dfis techniques avec enthousiasme.
                                </p>
                            </div>
                        </div>
                    </Col>
                </Row>
            </Container>
        </section>
    );
};